body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{background:#FFE135}body,html,#root{margin:0;padding:0;height:100%}h2{font-family:'Domine', serif;font-size:2.5rem;padding-right:30px;margin-bottom:0;letter-spacing:-3px}@media screen and (max-width: 375px){h2{font-size:2rem}}.page-header{display:flex}.header-text-container{display:flex;justify-content:center;width:100%}footer{position:fixed;bottom:0;width:100%}.footer-logo{height:35px;width:100%;display:flex;justify-content:center;margin-bottom:15px}.main-btn{font-family:'Courier Prime', sans-serif;font-size:1.3rem;border:4px solid #000;border-radius:45px;padding:5px 37px;text-decoration:none;color:black}button{border:none}.btn-back{margin-top:25px;flex-direction:column}.btn-back .icon-chevron-left{font-size:50px}@font-face{font-family:'icomoon';src:url(/Yolko/static/media/icomoon.8c754a81.eot);src:url(/Yolko/static/media/icomoon.8c754a81.eot) format("embedded-opentype"),url(/Yolko/static/media/icomoon.27a7019c.ttf) format("truetype"),url(/Yolko/static/media/icomoon.e4ef313f.woff) format("woff"),url(/Yolko/static/media/icomoon.6143d655.svg) format("svg");font-weight:normal;font-style:normal;font-display:block}[class^="icon-"],[class*=" icon-"]{font-family:'icomoon' !important;speak:never;font-style:normal;font-weight:normal;font-feature-settings:normal;font-variant:normal;text-transform:none;line-height:1;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.icon-chevron-left:before{content:"\e900"}

.page-container{position:relative}.joke-container{position:absolute;top:50%;left:50%;margin:-300px;max-width:300px;background:transparent;transform:rotate(-13deg) translateX(50%);font-size:1.3rem}.joke-container p,.joke-container b{background:none;font-family:'Courier Prime', sans-serif;animation:pop 0.8s infinite}@keyframes pop{from{transform:scale(0.97)}50%{transform:scale(1)}to{transform:scale(0.97)}}.logo-container{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100vh}

.logo svg{height:220px;margin-bottom:70px}

