.page-container {
    height: 100%;
}

.page-content {
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.egg-selector-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.egg-card {
    text-align: center;
    margin: 20px;
    min-width: 100px;
}

.eggIcon {
    height: 90px;
    width: 100px;
}

.eggItem a{
    text-decoration: none;
    color: black
}

.eggName {
  font-family: 'Courier Prime', sans-serif;
  font-size: 1.2rem;
  text-decoration: none;
  color: black;
  font-weight: bold;
}
